import { AppAsideToggler, AppHeaderDropdown, AppNavbarBrand, AppSidebarToggler } from '@coreui/react';
import PropTypes from 'prop-types';
import React, { useContext, useState } from 'react';
import { NavLink } from 'react-router-dom';
import { Badge, Button, DropdownItem, DropdownMenu, DropdownToggle, Nav, NavItem } from 'reactstrap';
import logo from '../../assets/img/brand/logo.png';
import sygnet from '../../assets/img/brand/logoSmall.png';
import { UserContext } from '../../context/contexts';
import firebase from '../../firebase/Firebase';



const propTypes = {
  children: PropTypes.node,
};

const defaultProps = {};



function DefaultHeader(props) {


  const { uid, displayName } = useContext(UserContext);



  const signOut = () => {
    firebase.auth().signOut();
  }






  const profileIconDropDown = uid ?
    <DropdownMenu right style={{ right: 'auto' }}>
      <NavLink to="/profile"><DropdownItem><i className="fa fa-edit"></i>Profile</DropdownItem></NavLink>
      <DropdownItem onClick={signOut}><i className="fa fa-lock"></i> Logout</DropdownItem>
      <NavLink to="/delete"><DropdownItem><i className="fa fa-user-times"></i> Delete Account</DropdownItem></NavLink>
    </DropdownMenu>
    :
    <DropdownMenu right style={{ right: 'auto' }}>
      <NavLink to="/login"><DropdownItem><i className="fa fa-lock"></i> Login</DropdownItem></NavLink>
    </DropdownMenu>;

  // eslint-disable-next-line
  const { children, ...attributes } = props;

  return (
    <React.Fragment>
      <AppSidebarToggler className="d-lg-none" display="md" mobile />
      <AppNavbarBrand
        full={{ src: logo, width: 144, height: 48, alt: 'NepseChart Logo' }}
        minimized={{ src: sygnet, width: 48, height: 48, alt: 'NepseChart Logo' }}
      />
      <AppSidebarToggler className="d-md-down-none" display="lg" />

      <Nav className="ml-auto" navbar>
        <NavItem className="px-3 d-none d-sm-block">
          <NavLink to="/profile">{displayName}</NavLink>
        </NavItem>
        <AppHeaderDropdown direction="down">
          <DropdownToggle nav>
            <i className="cui-user" alt={displayName} />
          </DropdownToggle>
          {profileIconDropDown}
        </AppHeaderDropdown>
      </Nav>


      <AppAsideToggler className="d-md-down-none" />
      <AppAsideToggler className="d-lg-none" mobile />
    </React.Fragment>
  );
}


DefaultHeader.propTypes = propTypes;
DefaultHeader.defaultProps = defaultProps;

export default DefaultHeader;
